// CSSStyle
@import 'vendor/csstyle';
@import 'Dropzone';

.rwp-users-reviews-toolbar {
	margin-bottom: 30px;
	font-size: 14px;

	span {
		font-weight: bold;
	}

	select {
		display: inline-block;
		background-color: #fff;
		border-color: #ddd;
		border-radius: 0;
		font-size: 14px;
		&:focus {
			outline: none;
		}
	}
}

/* ==========  Select Field ========== */
/* Style Select Field */
.rwp-select select {
  font-family: inherit;
  background-color: transparent;
  width: 100%;
  padding: 4px 0;
  font-size: inherit;
  color: inherit;
  border: none;
  border-bottom: 1px solid #e5e5e5;
  height: auto;
  margin: 0;
}

/* Remove focus */
.rwp-select select:focus {
  outline: none;
}

/* Hide label */
.rwp-select label {
  display: none;
}

/* Use custom arrow */
.rwp-select select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.rwp-select {
  position: relative;
}

.rwp-select:after {
  position: absolute;
  top: 0.75em;
  right: 0.5em;
  /* Styling the down arrow */
  width: 0;
  height: 0;
  padding: 0;
  content: '';
  border-left: .25em solid transparent;
  border-right: .25em solid transparent;
  border-top: .375em solid #a5a5a5;
  pointer-events: none;
}

.rwp-box__sorting {
  margin-bottom: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
}

.rwp-box__sorting i {
  display: block;
  margin-right: 5px;
  -ms-flex-preferred-size: 65px;
      flex-basis: 65px;
  color: #a5a5a5;
  font-size: 12px;
}

.rwp-users-reviews .rwp-u-review:last-child {
	margin-bottom: 0;
}

@include component(rwp-u-review) {
	padding: 10px;
	margin-bottom: 30px;
	min-height: 100px;
	position: relative;
	
	@include option(rwp-highlighted){
	    background-color: #f7f7f7;
	}

	@include part(user) {
		position: absolute;
	    top: 10px;
	    left: 10px;
	    width: 70px;
	}

	@include part(avatar) {
		display: block;
		width: 50px;
		border-radius: 50px;
		margin-bottom: 5px;
	}

	@include part(userinfo) {
		margin-bottom: 10px;
		font-size: 0;
	}

	@include part(username) {
		word-wrap: break-word;  
		font-weight: bold;
		font-size: 12px;
		display: inline-block;
		vertical-align: middle;
		margin: 0 10px 0 0;

		img + & {
			width: 50px;
			text-align: center;
		}
	}

	@include part(badges) {
		display: inline-block;
		vertical-align: middle;
	}

	@include part(badge) {
		background: #E91E63;
		display: inline-block;
	    color: #fff;
	    font-size: 11px;
	    font-weight: 700;
	    padding: 4px 6px;
	    line-height: 11px;
	    border-radius: 2px;
	}

	@include part(content) {
	    margin-left: 80px;
		min-height: 80px;

		@include option(rwp-no-avatar) {
			margin-left: 0;
		}
	}

	@include part(scores) {
		.rwp-review-criterion:last-child {
			margin-right: 0;
		}
	}

	.rwp-numeric-rating {
		margin-bottom: 10px;
		
		span {
			display: inline-block;
			font-size: 32px;
			line-height: 1em;
		}

		i {
			display: inline-block;
			font-size: 16px;
			line-height: 1em;
			font-style: normal;
		}
	}


	@include part(title) {
		font-weight: bold;
		margin-bottom: 10px;
		display: block;
	}

	@include part(comment) {
		margin-bottom: 10px;
	}

	@include part(images) {
		font-size: 0;
	}

	@include part(image) {
		overflow: hidden;
		display: inline-block;
		margin: 0 10px 10px 0;
		padding: 0;
		text-decoration: none !important;
		box-shadow: none !important;
		color: #323232 !important;
		border-radius: 50%;
		
		&:last-child {
			margin-right: 0;
		}
	}

	@include part(thumb) {
		display: block;
	    height: 100px;
	    width: 100px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		border-radius: 50%;

		border: none !important;
		-webkit-transition: opacity .3s ease,-webkit-transform .3s ease;
        -moz-transition: opacity .3s ease,-moz-transform .3s ease;
        -o-transition: opacity .3s ease,-o-transform .3s ease;
        transition: opacity .3s ease,transform .3s ease;
        -webkit-backface-visibility: hidden;

        &:hover {
            border: none;
            -webkit-transform: scale(1.05);
            -moz-transform: scale(1.05);
            -o-transform: scale(1.05);
            transform: scale(1.05);
            // opacity: .8;
        }
	}

	@include part(actions) {
		@include clearfix;
		margin-bottom: 10px;
	}

	@include part(sharing) {
		float: left;
		margin-top: 6px;
	}

	@include part(sharing-icon) {
		display: inline-block;
		width: 18px;
		height: 18px;
		font-size: 18px;
		line-height: 1;
		font-family: dashicons;
		text-decoration: inherit;
		font-weight: 400;
		font-style: normal;
		vertical-align: top;
		text-align: center;
		color: #909090;
		border: none !important;
		margin-right: 20px;
    	box-shadow: none !important;

		&:hover {
			color: #a5a5a5;
		}

			@include option(rwp-facebook){ 
				&:before {
              	//color: #3B5998;
              	content: "\f305";
            	}
            }
            @include option(rwp-twitter){ 
            	&:before {
              	//color: #55ACEE;
              	content: "\f301";
            	}
            }
            @include option(rwp-google){ 
            	&:before {
              	//color: #dd4b39;
              	content: "\f462";
            	}
            }
            @include option(rwp-email){ 
            	&:before {
              	//color: #666;
              	content: "\f465";
            	}
            }
            @include option(rwp-link){ 
            	&:before {
              	//color: #666;
              	content: "\f103";
            	}
            }
	}

	@include part(likes) {
		float: right;
	}

	@include part(judge-icons) {
		display: inline-block;
		vertical-align: middle;
		color: #a5a5a5;

		.dashicons-yes {
			color: #26a4c0;
		}
		.dashicons-warning {
			color: #ff9500;
		}
	}

	@include part(positive) {
		display: inline-block;
		vertical-align: middle;
	}
	@include part(positive-count) {
		display: inline-block;
		vertical-align: middle;
		font-size: 12px; 
		color: #56b258;
		font-weight: bold;
	}
	@include part(positive-icon) {
		display: inline-block;
		vertical-align: middle;
		width: 30px;
		height: 30px;
		background-image: url(../images/sprite-likes.png);
        background-repeat: no-repeat;
        background-size: 60px;
        background-position: 0px 0px;
        margin: 0 3px;

        &:hover {
			cursor: pointer;
		}
	}

	@include part(negative) {
		display: inline-block;
		vertical-align: middle;
	}
	@include part(negative-count) {
		display: inline-block;
		vertical-align: middle;
		font-size: 12px; 
		color: #f44336;
		font-weight: bold;
	}
	@include part(negative-icon) {
		display: inline-block;
		vertical-align: middle;
		width: 30px;
		height: 30px;
		background-image: url(../images/sprite-likes.png);
	    background-repeat: no-repeat;
	    background-size: 60px;
	    background-position: -30px 0px;
	    margin: 0 3px;

	    &:hover {
			cursor: pointer;
		}
	}

	@include part(date) {
		color: #a5a5a5;
		font-size: 12px;
		display: block;
	}

	@include part(judge-msg) {
		font-size: 12px;
		text-align: right;
		color: #a5a5a5;
	}
}

@include component(rwp-review-criterion) {
	display: inline-block;
	margin: 0 20px 10px 0;
	vertical-align: top;

	@include option (rwp-bs) {
		width: 150px;
	} 

	@include part(labels) {
		@include clearfix;
		margin-bottom: 5px;
	}

	@include part(label) {
		display: block;
		float: left;
		font-size: 12px;
		line-height: 12px;
		font-weight: bold;
	}

	@include part(score) {
		display: block;
		float: right;
		font-size: 12px;
		line-height: 12px;
		font-weight: bold;
	}

	@include part(base) {
		display: block;
		width: 100%;
		background-color: #eee;
		height: 10px;
	}

	@include part(value) {
		display: block;
		background-color: #2196f3;
		height: inherit;
		box-shadow: inset 0px -2px 0px 0px rgba(0, 0, 0, 0.4);
	}
}

@include component(rwp-five-stars)  {
    $size: 16px;
    width: $size * 5;
    height: $size;
    background-size: $size;
    background-position: 0 (-$size);

    div {
        background-position: 0 0;
        background-size: $size;
        background-image: inherit;
        display: block;
        height: $size;
    }   

    @include option(rwp-ss) {
    	margin-bottom: 10px;
    }
}

@include component(rwp-loading-icon) {
	display: block;
    width: 50px;
    height: 50px;
    background-image: url(../images/loader-3.gif);
    background-size: 50px;
    margin: 0 auto;
}

@include component(rwp-more-urs-btn) {
	font-weight: bold;
    text-align: center;
    display: block;
    text-transform: uppercase;
    margin-top: 20px;

    &:hover {
    	cursor: pointer;
    }
}

@include component(rwp-pagination) {
	@include clearfix;
	margin-top: 20px;
		
	@include part(container) {
		float: right;

		span {
			display: inline-block;
		    margin-left: 2px;
		    font-size: 12px;
		    line-height: 22px;
		    text-align: center;
		    /* padding: 4px 6px; */
		    width: 22px;
		    height: 22px;

			&:hover {
				cursor: pointer;
			}
		}
	}

	@include part(current) {
		background: #424242 !important;
    	color: #fff;
    	border-radius: 20px;
	}
}

@include component(rwp-notification) {
	padding: 4px 15px;
    margin: 0 0 10px 0;
	display: none;
    border-left: 2px solid #56b258;
    width: 100%;

    @include option(rwp-notice-with-error) {
    	border-left: 2px solid #f44336;
    }
}
