.dropzone {
    min-height: 170px;
    border: 2px dashed #e5e5e5;
    background: #fff;
    padding: 0;
    margin-bottom: 10px;

    &.dz-drag-hover {
        border-style: dashed;
        border-color: #2196F3;
    }
    .dz-message {
        color: #c0c0c0;
        margin: 5em 0;
    }

    .dz-preview {
        position: relative;
        display: inline-block;
        vertical-align: top;
        margin: 10px;
        min-height: auto;
    
        &:hover .dz-image img {
            -webkit-filter: blur(0px);
            filter: blur(0px);
            -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
        }
        .dz-image {
            border-radius: 0;
        }
        &.dz-file-preview {
            .dz-image {
                border-radius: 0;
                background: #e5e5e5;
            }
        }
        .dz-progress {
            border-radius: 2px;
            height: 6px;
            margin-top: -12px;
        }
        .dz-progress .dz-upload {
            background: #323232;
        }
        .dz-remove {
            box-shadow: none;
            color: #3d3d3d;
            font-size: 11px;
            text-decoration: none;
            margin: 2px 0 0 0;
            padding: 0
        }
        .dz-success-mark, .dz-error-mark {
            margin-top: -36px;
        }
        .dz-details {
            display: none;
        }
        .dz-error-message {
            border-radius: 4px;
            font-size: 12px;
            text-align: center;
            top: 150px;
            background: #E53935;
            padding: 4px;
        }
        .dz-error-message:after {
            border-bottom: 6px solid #E53935;
        }
    }
}
