[v-cloak] {
  display: none !important;
}

.rwp-rosu {
  max-width: 540px;
  line-height: 1em;
  font-size: 12px;
}

.rwp-rosu__loader {
	display: block;
    width: 50px;
    height: 50px;
    background-image: url(../images/loader-3.gif);
    background-size: 50px;
    margin: 0 auto;
}

.rwp-rosu__notice {
	font-size: 14px;
	text-align: center;
	margin: 1em 0;
	color: #880E4F;
}

.rwp-rosu__user {
  display: flex;
  align-content: center;
}

.rwp-rosu__info {
    align-self: center;
}

.rwp-rosu__avatar {
  margin-right: 20px;
  display: block;
  text-decoration: none !important;
  border: 0 !important;
  box-shadow: none !important;

  img {
    display:block;
    border-radius: 50%;
    box-shadow: none !important;
    width: 80px;
  }
}

.rwp-rosu__displayname {
  font-size: 16px;
  display: block;
  font-weight: bold;
}

.rwp-rosu__badges {
  list-style: none !important;
  margin: 0 !important;
  padding: 0 !important;
  display: flex;
  align-content: center;
  flex-wrap: wrap;

  li {
    list-style: none !important;
    margin: 10px 20px 0px 0 !important;
    padding: 0 !important;
  }

  i {
    font-size: 16px !important;
    margin-right: 5px;
  }

  i, span {
    display: inline-block;
    vertical-align: middle;
  }
}

.rwp-rosu__badge-icon-star i {
  color: #ff9500 !important;
}

.rwp-rosu__badge-icon-camera i {
  color: #3f51b5 !important;
}

.rwp-rosu__badge-icon-thumbup i {
  color: #56b258 !important;
}

.rwp-rosu__badge-icon-verified i {
  color: #e91e63 !important;
}

.rwp-rosu__seperator {
    color: #ccc;
    width: 100%;
    margin: 20px auto;
    overflow: hidden;
    text-align: center;
    line-height: 1.2em;
    font-weight: bold;

    span {
      margin: 0 20px 0 24px;
      display: inline-block;
    }

    &::before, &::after {
      border-bottom: 1px solid #e5e5e5;
      content: "";
      display: inline-block;
      height: .65em;
      margin: 0 -4px 0 -100%;
      vertical-align: top;
      width: 50%;
  }

    &::after {
      margin: 0 -100% 0 0;
    }
}

.rwp-rosu__review {
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px dashed #e5e5e5;
  position: relative;
}

.rwp-rosu .rwp-rosu__review:last-child {
  border-bottom: 0;
}

.rwp-rosu__review-show {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 18px;
  color: #a5a5a5;
}

.rwp-rosu__review-post {
  display: block;
  text-decoration: none !important;
  border: 0 !important;
  color: #3d3d3d;
  font-weight: bold;
  padding-right: 20px;
  box-shadow: none !important;
}

.rwp-rosu__review-badge {
    background: #e91e63;
    border-radius: 2px;
    display: inline-block;
    color: #fff;
    font-weight: bold;
    font-size: 10px;
    line-height: 1em;
    padding: 4px 6px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 10px;
}

.rwp-rosu__review-title {
  display: block;
  font-weight: bold;
  font-size: 14px;
  margin-top: 10px;
  line-height: 1.2em;

}

.rwp-rosu__review-comment {
  display: block;
  font-size: 14px;
  margin: 10px 0 0 0;
  line-height: 1.8em;
  text-align: justify;
}

.rwp-rosu__review-images {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;

  span {
    display: block;
    border-radius: 50%;
    margin: 0 10px 10px 0;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;

    &:hover {
      cursor: pointer;
    }
  }
}

.rwp-rosu__review-date {
  color: #a5a5a5;
  font-size: 14px;
  margin-top: 10px;
  display: block;
}

.rwp-rosu__review-score {
  margin-top: 10px;
}

.rwp-rosu__numeric-score {
    margin-bottom: 10px;

    span {
      display: inline-block;
      font-size: 32px;
      line-height: 1em;
    }

    i {
      display: inline-block;
      font-size: 16px;
      line-height: 1em;
      font-style: normal;
    }
  }
